import React, { useRef } from "react";
import styles from "./laboratory.module.css";

export default function Laboratories() {
  const sliderRef = useRef(null);

  // Dynamic data structure
  const laboratoriesData = {
    subtitle: "LABORATORIES",
    title: "CUTTING-EDGE & SPECIALIZED THE CSE DEPARTMENT",
    description:
      "futurns several specialized labs, each focused on critical technical domains.",
    labs: [
      {
        id: 1,
        title: "CORE LABS",
        image: "/path-to-image1.jpg",
      },
      {
        id: 2,
        title: "OPERATING SYSTEM & MULTIMEDIA LAB",
        image: "/path-to-image2.jpg",
      },
      {
        id: 3,
        title: "SOFTWARE ENGINEERING & WEB DEVELOPMENT LAB",
        image: "/path-to-image3.jpg",
      },
      {
        id: 4,
        title: "COMPUTATIONAL & PROGRAMMING LAB",
        image: "/path-to-image4.jpg",
      },
      {
        id: 5,
        title: "ARTIFICIAL INTELLIGENCE LAB",
        image: "/path-to-image5.jpg",
      },
    ],
    applyButton: "APPLY NOW",
  };

  const scrollLeft = () => {
    if (sliderRef.current) {
      sliderRef.current.scrollBy({ left: -350, behavior: "smooth" });
    }
  };

  const scrollRight = () => {
    if (sliderRef.current) {
      sliderRef.current.scrollBy({ left: 350, behavior: "smooth" });
    }
  };

  return (
    <div className={styles.container}>
      <div className="container-fluid">
        {/* Header Section */}
        <div className={styles.headerSection}>
          <p className={styles.subtitle}>{laboratoriesData.subtitle}</p>
          <h2 className={styles.title}>{laboratoriesData.title}</h2>
          <p className={styles.description}>{laboratoriesData.description}</p>

          {/* Navigation Arrow */}
          <button className={styles.scrollIndicator} onClick={scrollRight}>
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
            >
              <path d="M12 5v14M5 12l7 7 7-7" />
            </svg>
          </button>
        </div>

        {/* Slider Container */}
        <div className={styles.sliderWrapper}>
          {/* Lab Cards Slider */}
          <div className={styles.slider} ref={sliderRef}>
            {laboratoriesData.labs.map((lab) => (
              <div key={lab.id} className={styles.labCard}>
                <div className={styles.imageContainer}>
                  <div className={styles.imagePlaceholder}>
                    <svg
                      width="60"
                      height="60"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="1"
                    >
                      <rect x="3" y="3" width="18" height="18" rx="2" ry="2" />
                      <circle cx="8.5" cy="8.5" r="1.5" />
                      <polyline points="21 15 16 10 5 21" />
                    </svg>
                    <p className={styles.imageText}>IMAGE</p>
                  </div>
                  {/* Uncomment when you have actual images */}
                  {/* <img src={lab.image} alt={lab.title} className={styles.labImage} /> */}

                  {/* Gradient Overlay */}
                  <div className={styles.gradientOverlay}></div>

                  {/* Lab Info */}
                  <div className={styles.labInfo}>
                    <h3 className={styles.labTitle}>{lab.title}</h3>
                    <button className={styles.viewButton}>
                      <svg
                        width="24"
                        height="24"
                        viewBox="0 0 24 24"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth="2"
                      >
                        <circle cx="12" cy="12" r="10" />
                        <path d="M12 8v8M8 12h8" />
                      </svg>
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Apply Button */}
        <div className={styles.buttonWrapper}>
          <button className={styles.applyButton}>
            {laboratoriesData.applyButton}
          </button>
        </div>
      </div>

      {/* Bootstrap CSS CDN */}
      <link
        href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css"
        rel="stylesheet"
      />
    </div>
  );
}
